.App {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.main {
  display:flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border: 14px outset lime;
  width: fit-content;
  min-width:240px;
  min-height: 400px;
}

.options {
  display:flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 200px;
}

#timer {
  display: none;
}

#correct{
  display: none;
  opacity:0.7;
  transition: opacity 1s;
  left:50%;
  top:50%;
  font-size: 50px;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  z-index: 1001;
}

.stats {
  display:flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.stats p {
  display:block;
}